import store from './../components/vuex/store'
import {mapStore} from 'vuex'
export default {
    data(){
        return{
            bookList:new Set([]),
            isEmpty:true,
        }
    },
    mounted(){
        this.bookList = this.$store.state.user.readbooks;
    },
    methods:{
        toMain(){
            this.$router.push('/');
        }
    },
    computed:{
        books(){
            if(this.bookList.size === 0){
                return '你的书架空空如也'
            }else{
                this.isEmpty = false;
                let arr = [];
                for(let i of this.bookList){
                    arr.push(i);
                }
                return arr;
            }
        }
    },
    store
}



