
import store from './../components/vuex/store'
import {mapState} from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
data(){
return{
bookid:'',
index:0,
list:['图书介绍','评论','更多信息'],
list1:'图书介绍',
isStretch:false,
booklists:[
{
id:'re1',
name:'狼群'
},
{
id:'re2',
name:'朝闻道'
},
{
id:'re3',
name:'活着活着就老了'
},
{
id:'re4',
name:'银河帝国'
},
{
id:'re5',
name:'简约至上'
},
{
id:'re6',
name:'下一个淘宝精英卖家就是你'
},
{
id:'new1',
name:'电商产品经理宝典：电商后台系统'
},
{
id:'new2',
name:'有效学习'
},
{
id:'new3',
name:'增长黑客实战'
},
{
id:'new4',
name:'如何让他买：改变消费者行为的十大策略'
},
{
id:'new5',
name:'平衡掌控者：游戏数值战斗设计'
},
{
id:'new6',
name:'看得见的世界史：英国'
}
]
}
},
components:{
XButton,
Tab,
Swiper,
SwiperItem,
TabItem
},
created(){
this.bookid = this.$route.params.id;
},
computed:{
bookdetail(){
let id = this.bookid;
return this.$store.state.bookDetial[id]
}
},
mounted(){
let t = this.bookdetail.description;
this.$refs.text.innerHTML = t;
},
methods:{
backPage(){
this.$router.push('/');
},
stretch(){
this.$refs.text.style.height = '250px';
this.isStretch = true;
},
hidden(){
this.$refs.text.style.height = '140px';
this.isStretch = false;
},
changeReadModelpage(s){
let account = this.$store.state.user.account;
let password = this.$store.state.user.password;
if(account && password){
this.$store.state.user.readbooks.add(this.bookdetail.name);
}
this.$router.push({path:'/readbook/' + s});
}
},
store
}



