
import Search from 'vux/src/components/search/index.vue'

export default {
data(){
return{
results:[]
}
},
components:{
Search
},
methods:{
toMain(){
this.$router.push('/');
},
getResult (val) {
this.results = val ? getResult(this.value) : [];
}
}
}
function getResult (val) {
let rs = []
for (let i = 0; i < 20; i++) {
rs.push({
title: `${val} result: ${i + 1} `,
other: i
})
}
return rs
}



