
import store from './../components/vuex/store'
import {mapState} from 'vuex'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

export default {
data(){
return{
id: '',
}
},
created(){
this.id = this.$route.params.id;
},
computed:{
detail(){
let id = this.id;
return this.$store.state[id];
}
},
components:{
ToastPlugin
},
store,
methods:{
backPage(){
this.$router.push('/');
},
doNotHasCopyRight(){
this.$vux.toast.text('由于版权问题，不能阅读，请阅读首页中精品推荐和新书上架中的书籍', 'middle');
}
}

}



