

import Vue from 'vue'
import App from './App'
import router from './router'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.config.productionTip = false
Vue.use(ToastPlugin,{time:5000})

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



